// packages/dancycodes/hyper-front/src/hyper-ext/plugins/backend/watchers/globalNavigate.ts
// Icon: material-symbols:public
// Slug: Enhanced global navigation with comprehensive options support.
// Description: Provides consistent navigation API across all frontend/backend interactions.

import type { WatcherPlugin } from "../../../../datastar/engine/types";

// Global flag to ensure this runs only once
let globalNavigateSetup = false;

export const GlobalNavigate: WatcherPlugin = {
    type: "watcher",
    name: "globalNavigate",
    onGlobalInit: (ctx) => {
        // Only initialize once globally
        if (!globalNavigateSetup) {
            globalNavigateSetup = true;
            setupEnhancedGlobalNavigation(ctx);
        }
    },
};

/**
 * Navigation options interface - matches backend exactly
 */
interface NavigationOptions {
    merge?: boolean; // Whether to merge with existing query parameters
    only?: string[]; // Only preserve these query parameters
    except?: string[]; // Preserve all query parameters except these
    replace?: boolean; // Use replaceState instead of pushState
}

/**
 * Set up enhanced global navigation functions with comprehensive options support
 *
 * Provides perfect consistency between:
 * - Frontend: data-navigate, @navigate()
 * - Backend: hyper()->navigate(), hyper()->navigateWith(), etc.
 */
function setupEnhancedGlobalNavigation(ctx: any) {
    const { actions, startBatch, endBatch } = ctx;

    /**
     * Enhanced navigation with comprehensive options support
     *
     * @param url Target URL or query object
     * @param key Navigation key for Datastar routing
     * @param options Navigation options (merge, only, except, replace)
     */
    const navigateWithOptions = (
        url: string,
        key: string = "true",
        options: NavigationOptions = {}
    ) => {
        try {
            startBatch();

            // Get the GET action from Datastar's action registry
            const getAction = actions.get || actions.GET;

            if (!getAction) {
                throw new Error(
                    "GET action not found in Datastar actions registry"
                );
            }

            // Prepare fetch arguments with navigate headers
            const fetchArgs = {
                headers: {
                    "HYPER-NAVIGATE": "true",
                    "HYPER-NAVIGATE-KEY": key,
                },
            };

            // Create proper runtime context
            const runtimeCtx = {
                ...ctx,
                el: document.body,
            };

            // Call GET action using Datastar's infrastructure
            getAction.fn(runtimeCtx, url, fetchArgs);

            // Handle browser history
            setTimeout(() => {
                if (options.replace) {
                    history.replaceState(null, "", url);
                } else {
                    history.pushState(null, "", url);
                }
            }, 0);
        } catch (error) {
            console.error("Enhanced navigate failed:", error);
            // Fallback to normal navigation
            window.location.href = url;
        } finally {
            endBatch();
        }
    };

    /**
     * BASIC NAVIGATION FUNCTIONS
     */

    // Basic hyperNavigate - backward compatible
    (window as any).hyperNavigate = (url: string, key: string = "true") => {
        navigateWithOptions(url, key, {});
    };

    // Enhanced navigation with options - used by backend
    (window as any).hyperNavigateWithOptions = navigateWithOptions;

    /**
     * SPECIALIZED NAVIGATION FUNCTIONS
     */

    // Navigate with explicit merge behavior
    (window as any).hyperNavigateWith = (
        url: string,
        key: string = "true",
        merge: boolean = false,
        options: Partial<NavigationOptions> = {}
    ) => {
        navigateWithOptions(url, key, { ...options, merge });
    };

    // Navigate and merge with current parameters
    (window as any).hyperNavigateMerge = (
        url: string,
        key: string = "true",
        options: Partial<NavigationOptions> = {}
    ) => {
        navigateWithOptions(url, key, { ...options, merge: true });
    };

    // Navigate with clean slate (no merging)
    (window as any).hyperNavigateClean = (
        url: string,
        key: string = "true",
        options: Partial<NavigationOptions> = {}
    ) => {
        navigateWithOptions(url, key, { ...options, merge: false });
    };

    // Navigate preserving only specific parameters
    (window as any).hyperNavigateOnly = (
        url: string,
        only: string[],
        key: string = "true"
    ) => {
        navigateWithOptions(url, key, { merge: true, only });
    };

    // Navigate preserving all except specific parameters
    (window as any).hyperNavigateExcept = (
        url: string,
        except: string[],
        key: string = "true"
    ) => {
        navigateWithOptions(url, key, { merge: true, except });
    };

    // Navigate using replaceState
    (window as any).hyperNavigateReplace = (
        url: string,
        key: string = "true",
        options: Partial<NavigationOptions> = {}
    ) => {
        navigateWithOptions(url, key, { ...options, replace: true });
    };

    /**
     * ENHANCED BACK/REFRESH FUNCTIONS
     */

    // Enhanced hyperBack with options support
    (window as any).hyperBackWithOptions = (
        fallbackUrl: string = "/",
        key: string = "back",
        options: NavigationOptions = {}
    ) => {
        try {
            let backUrl = fallbackUrl;

            // Try to get the previous URL from browser history
            if (
                document.referrer &&
                document.referrer !== window.location.href
            ) {
                const referrerUrl = new URL(document.referrer);
                const currentUrl = new URL(window.location.href);

                // Only use referrer if it's from the same origin
                if (referrerUrl.origin === currentUrl.origin) {
                    backUrl = document.referrer;
                }
            }

            // Navigate reactively with the specified options
            navigateWithOptions(backUrl, key, options);
        } catch (error) {
            console.error("Enhanced hyperBack failed:", error);
            // Fallback to simple history.back()
            if (history.length > 1) {
                history.back();
            } else {
                window.location.href = fallbackUrl;
            }
        }
    };

    // Enhanced hyperRefresh with options support
    (window as any).hyperRefreshWithOptions = (
        key: string = "refresh",
        options: NavigationOptions = {}
    ) => {
        try {
            const currentUrl = window.location.href;
            navigateWithOptions(currentUrl, key, options);
        } catch (error) {
            console.error("Enhanced hyperRefresh failed:", error);
            // Fallback to regular refresh
            window.location.reload();
        }
    };

    /**
     * CONVENIENCE FUNCTIONS
     */

    // Update current page queries
    (window as any).hyperUpdateQueries = (
        queries: Record<string, any>,
        key: string = "update",
        merge: boolean = true
    ) => {
        const currentPath = window.location.pathname;
        const queryString = buildQueryString(queries);
        const url = queryString ? `${currentPath}?${queryString}` : currentPath;

        navigateWithOptions(url, key, { merge });
    };

    // Clear specific query parameters
    (window as any).hyperClearQueries = (
        paramNames: string[],
        key: string = "clear"
    ) => {
        const clearQueries = paramNames.reduce((acc, name) => {
            acc[name] = null;
            return acc;
        }, {} as Record<string, null>);

        (window as any).hyperUpdateQueries(clearQueries, key, true);
    };

    // Reset pagination while preserving filters
    (window as any).hyperResetPagination = (key: string = "pagination") => {
        (window as any).hyperUpdateQueries({ page: 1 }, key, true);
    };

    /**
     * BACKWARD COMPATIBILITY FUNCTIONS
     */

    // Backward compatible hyperBack (without options)
    (window as any).hyperBack = (
        fallbackUrl: string = "/",
        key: string = "back"
    ) => {
        (window as any).hyperBackWithOptions(fallbackUrl, key, { merge: true });
    };

    // Backward compatible hyperRefresh (without options)
    (window as any).hyperRefresh = (key: string = "refresh") => {
        (window as any).hyperRefreshWithOptions(key, { merge: true });
    };

    // Simple reload function for hard refreshes
    (window as any).hyperReload = () => {
        window.location.reload();
    };

    /**
     * DEBUGGING HELPERS
     */

    // Log current navigation state
    (window as any).hyperDebugNavigation = () => {
        console.group("ðŸ§­ Hyper Navigation Debug");
        console.log("Current URL:", window.location.href);
        console.log("Current Path:", window.location.pathname);
        console.log("Current Query:", window.location.search);
        console.log(
            "Current Queries:",
            Object.fromEntries(new URLSearchParams(window.location.search))
        );
        console.log("Referrer:", document.referrer);
        console.log("History Length:", history.length);
        console.groupEnd();
    };
}

/**
 * UTILITY FUNCTIONS
 */

/**
 * Build query string from object, handling various value types
 */
function buildQueryString(queries: Record<string, any>): string {
    const params = new URLSearchParams();

    for (const [key, value] of Object.entries(queries)) {
        if (value === null || value === undefined) {
            // Skip null/undefined values - they clear parameters
            continue;
        }

        if (Array.isArray(value)) {
            // Handle arrays (for multi-select, checkboxes, etc.)
            value.forEach((item) => {
                if (item !== null && item !== undefined && item !== "") {
                    params.append(key, String(item));
                }
            });
        } else if (value === "" || String(value).trim() === "") {
            // Skip empty strings
            continue;
        } else {
            // Handle primitive values
            params.set(key, String(value));
        }
    }

    return params.toString();
}

/**
 * EXPOSED API SUMMARY
 *
 * Basic Navigation:
 * - hyperNavigate(url, key?)
 * - hyperNavigateWithOptions(url, key?, options?)
 *
 * Explicit Control:
 * - hyperNavigateWith(url, key?, merge?, options?)
 * - hyperNavigateMerge(url, key?, options?)
 * - hyperNavigateClean(url, key?, options?)
 * - hyperNavigateOnly(url, only[], key?)
 * - hyperNavigateExcept(url, except[], key?)
 * - hyperNavigateReplace(url, key?, options?)
 *
 * Enhanced Back/Refresh:
 * - hyperBackWithOptions(fallback?, key?, options?)
 * - hyperRefreshWithOptions(key?, options?)
 *
 * Convenience:
 * - hyperUpdateQueries(queries{}, key?, merge?)
 * - hyperClearQueries(names[], key?)
 * - hyperResetPagination(key?)
 *
 * Utilities:
 * - hyperReload()
 * - hyperDebugNavigation()
 *
 * Backward Compatible:
 * - hyperBack(fallback?, key?)
 * - hyperRefresh(key?)
 */
