// Icon: material-symbols:check-circle-outline
// Slug: Conditionally renders elements based on expression.
// Description: Implements conditional rendering like Alpine.js x-if - must be on <template> with single root element. Uses computed wrapper for proper reactivity with Datastar's signal system.

import type { AttributePlugin, HTMLOrSVG } from "../../../datastar/engine/types";
import { apply } from "../../../datastar/engine";

/**
 * State management interface for each data-if instance
 */
interface IfState {
    renderedElement: Element | null;
    isRendered: boolean;
    commentMarker: Comment;
    effectCleanup: (() => void) | null;
    ifId: string;
}

// WeakMap to store if state per element
const ifStates = new WeakMap<HTMLOrSVG, IfState>();
let ifCounter = 0;

export const If: AttributePlugin = {
    type: "attribute",
    name: "if",
    keyReq: "denied",
    valReq: "must",
    shouldEvaluate: true,
    returnsValue: true,
    onLoad: ({ el, effect, rx, runtimeErr, computed }) => {
        // VALIDATION: Must be on template element
        if (!(el instanceof HTMLTemplateElement)) {
            throw runtimeErr("IfMustBeOnTemplate", {
                message: "data-if must be used on <template> elements",
            });
        }

        // VALIDATION: Template must have single root element (Alpine.js requirement)
        const templateContent = el.content;
        const rootElements = Array.from(templateContent.children);
        if (rootElements.length !== 1) {
            throw runtimeErr("IfTemplateMustHaveSingleRoot", {
                count: rootElements.length,
                message:
                    "data-if template must contain exactly one root element",
            });
        }

        const template = el;
        const container = template.parentElement!;

        // Hide template element
        template.style.display = "none";

        // ALPINE.JS PATTERN: Create comment marker for reliable positioning
        const ifId = `__if_${++ifCounter}`;
        const commentMarker = document.createComment(`Datastar if ${ifId}`);
        container.insertBefore(commentMarker, template.nextSibling);

        // Initialize state tracking with WeakMap (data-for pattern)
        const state: IfState = {
            renderedElement: null,
            isRendered: false,
            commentMarker,
            effectCleanup: null,
            ifId,
        };

        ifStates.set(el, state);

        try {
            // COMPUTED WRAPPER: Re-evaluate expression reactively (data-for pattern)
            // This ensures proper subscription even when signals are deleted/recreated by Datastar
            const conditionComputed = computed(() => {
                return rx(); // Evaluate reactive expression inside computed
            });

            // DATASTAR REACTIVE EFFECT: Conditional rendering
            const effectCleanup = effect(() => {
                const shouldRender = !!conditionComputed();

                // State transition: false -> true (RENDER)
                if (shouldRender && !state.isRendered) {
                    state.renderedElement = renderElement(
                        template,
                        state.commentMarker
                    );
                    state.isRendered = true;
                }
                // State transition: true -> false (CLEANUP)
                else if (!shouldRender && state.isRendered) {
                    if (state.renderedElement) {
                        cleanupElement(state.renderedElement);
                        state.renderedElement = null;
                    }
                    state.isRendered = false;
                }
            });

            state.effectCleanup = effectCleanup;

            // Return cleanup function (called when plugin is removed)
            return () => {
                cleanupIfInstance(state);
                ifStates.delete(el);
            };
        } catch (error) {
            // Cleanup on error
            const instanceState = ifStates.get(el);
            if (instanceState) {
                cleanupIfInstance(instanceState);
                ifStates.delete(el);
            }
            throw error;
        }
    },
};

/**
 * RENDER FUNCTION: Clone template and insert into DOM
 * Follows data-for pattern with queueMicrotask for Datastar processing
 */
function renderElement(
    template: HTMLTemplateElement,
    commentMarker: Comment
): Element {
    // Clone template content
    const clone = template.content.cloneNode(true) as DocumentFragment;
    const element = clone.firstElementChild as Element;

    // Insert element into DOM after the comment marker
    commentMarker.parentNode!.insertBefore(element, commentMarker.nextSibling);

    // Apply Datastar processing to generated element
    // Use queueMicrotask to ensure DOM is ready (data-for pattern)
    queueMicrotask(() => {
        apply(element as HTMLElement);
    });

    return element;
}

/**
 * CLEANUP FUNCTION: Remove element from DOM
 * Datastar's mutation observer handles attribute cleanup
 */
function cleanupElement(element: Element): void {
    element.remove();
}

/**
 * COMPREHENSIVE CLEANUP: Clean up entire if instance
 * Follows data-for's comprehensive cleanup pattern
 */
function cleanupIfInstance(state: IfState): void {
    // Cleanup reactive effect
    if (state.effectCleanup) {
        state.effectCleanup();
        state.effectCleanup = null;
    }

    // Remove rendered element if present
    if (state.renderedElement) {
        cleanupElement(state.renderedElement);
        state.renderedElement = null;
    }

    // Remove comment marker
    if (state.commentMarker && state.commentMarker.parentNode) {
        state.commentMarker.remove();
    }

    // Reset state
    state.isRendered = false;
}
