// packages/dancycodes/hyper-front/src/hyper-ext/plugins/attributes/navigate.ts
// Icon: material-symbols:navigation
// Slug: Enhanced reactive navigation with Datastar-standard modifiers.
// Description: Professional navigation with explicit merge control and standard modifier support.

import type { AttributePlugin } from "../../../datastar/engine/types";

/**
 * Enhanced Navigate Attribute - Professional Navigation System
 *
 * Follows Datastar modifier conventions for explicit, predictable navigation.
 * No more "smart defaults" - developers control exactly what happens.
 *
 * MODIFIER EXAMPLES:
 * data-navigate="true"                                    // Basic navigation (no merge)
 * data-navigate__key.sidebar="true"                       // Navigation with key
 * data-navigate__merge="true"                            // Explicit merge behavior
 * data-navigate__merge__key.main="true"                  // Merge with key
 * data-navigate__only.search,category="true"             // Only preserve specific params
 * data-navigate__except.page="true"                      // Preserve all except specific
 * data-navigate__replace="true"                          // Replace instead of push history
 * data-navigate__debounce.300ms="true"                   // Debounce navigation
 *
 * COMPLEX EXAMPLES:
 * data-navigate__merge__except.page__debounce.500ms="true"    // Comprehensive control
 * data-navigate__key.filters__only.search,category="true"     // Granular parameter control
 */
export const Navigate: AttributePlugin = {
    type: "attribute",
    name: "navigate",
    keyReq: "denied",
    valReq: "must",
    shouldEvaluate: false,
    onLoad: (ctx) => {
        const { el, value, mods, runtimeErr } = ctx;

        // Parse navigation configuration from modifiers
        const navigateConfig = parseNavigateModifiers(mods, value.trim());

        if (!navigateConfig) {
            throw runtimeErr("InvalidNavigateConfiguration", {
                value,
                modifiers: Array.from(mods.keys()),
            });
        }

        // Create timing wrapper if specified
        const executeNavigation = navigateConfig.timing
            ? createTimingWrapper(handleNavigation, navigateConfig.timing)
            : handleNavigation;

        // Create click handler
        const handleClick = (event: Event) => {
            const target = event.target as HTMLElement;
            const link = target.closest("a[href]") as HTMLAnchorElement;

            if (!link || !shouldInterceptNavigation(link)) {
                return;
            }

            event.preventDefault();
            executeNavigation(link.href, navigateConfig);
        };

        // Create form submit handler for GET forms
        const handleSubmit = (event: Event) => {
            const form = event.target as HTMLFormElement;

            if (!shouldInterceptFormSubmit(form)) {
                return;
            }

            event.preventDefault();

            const formData = new FormData(form);
            const url = new URL(form.action, window.location.href);

            // Add form data to URL params
            for (const [key, value] of formData.entries()) {
                if (typeof value === "string") {
                    url.searchParams.set(key, value);
                }
            }

            executeNavigation(url.toString(), navigateConfig);
        };

        // Attach event listeners
        el.addEventListener("click", handleClick);
        el.addEventListener("submit", handleSubmit);

        return () => {
            el.removeEventListener("click", handleClick);
            el.removeEventListener("submit", handleSubmit);
        };
    },
};

/**
 * Navigation configuration interface
 */
interface NavigateConfig {
    key: string;
    merge: boolean;
    only?: string[];
    except?: string[];
    replace?: boolean;
    timing?: TimingConfig;
}

interface TimingConfig {
    type: "debounce" | "throttle" | "delay";
    duration: number;
    leading?: boolean;
}

/**
 * Parse Datastar modifiers into navigation configuration
 *
 * SUPPORTED MODIFIERS:
 * - __key.{keyname} â†’ Sets navigation key
 * - __merge â†’ Enable query parameter merging
 * - __only.{param1,param2} â†’ Only preserve specific parameters
 * - __except.{param1,param2} â†’ Preserve all except specific parameters
 * - __replace â†’ Use replaceState instead of pushState
 * - __debounce.{duration}ms â†’ Debounce navigation
 * - __throttle.{duration}ms â†’ Throttle navigation
 * - __delay.{duration}ms â†’ Simple delay
 */
function parseNavigateModifiers(
    mods: Map<string, Set<string>>,
    value: string
): NavigateConfig | null {
    if (!value || value.trim() === "") {
        return null;
    }

    const config: NavigateConfig = {
        key: "true", // Default key
        merge: false, // EXPLICIT: No more smart defaults!
        replace: false,
    };

    // Parse modifiers
    for (const [modName, modTags] of mods) {
        switch (modName) {
            case "key":
                // __key.sidebar â†’ key = "sidebar"
                config.key = modTags.size > 0 ? Array.from(modTags)[0] : "true";
                break;

            case "merge":
                // __merge â†’ merge = true
                config.merge = true;
                break;

            case "only":
                // __only.search,category â†’ only = ["search", "category"]
                config.only = Array.from(modTags);
                config.merge = true; // Implied when using only/except
                break;

            case "except":
                // __except.page â†’ except = ["page"]
                config.except = Array.from(modTags);
                config.merge = true; // Implied when using only/except
                break;

            case "replace":
                // __replace â†’ replace = true
                config.replace = true;
                break;

            case "debounce":
                // __debounce.300ms â†’ debounce for 300ms
                config.timing = parseTimingModifier("debounce", modTags);
                break;

            case "throttle":
                // __throttle.500ms â†’ throttle for 500ms
                config.timing = parseTimingModifier("throttle", modTags);
                break;

            case "delay":
                // __delay.1s â†’ simple delay for 1s
                config.timing = parseTimingModifier("delay", modTags);
                break;
        }
    }

    // Validation: cannot use both only and except
    if (config.only && config.except) {
        console.warn(
            "Navigate: Cannot use both __only and __except modifiers. Using __only."
        );
        delete config.except;
    }

    return config;
}

/**
 * Parse timing modifier tags into timing configuration
 */
function parseTimingModifier(
    type: "debounce" | "throttle" | "delay",
    tags: Set<string>
): TimingConfig | undefined {
    const tagArray = Array.from(tags);

    // Find duration tag (e.g., "300ms", "1s", "1000")
    const durationTag = tagArray.find((tag) => /^\d+(?:ms|s)?$/.test(tag));
    if (!durationTag) {
        console.warn(
            `Navigate: Invalid ${type} timing - no duration specified`
        );
        return undefined;
    }

    // Parse duration
    let duration: number;
    if (durationTag.endsWith("ms")) {
        duration = parseInt(durationTag.slice(0, -2));
    } else if (durationTag.endsWith("s")) {
        duration = parseInt(durationTag.slice(0, -1)) * 1000;
    } else {
        duration = parseInt(durationTag); // Assume milliseconds
    }

    return {
        type,
        duration,
        leading: tagArray.includes("leading"),
    };
}

/**
 * Create timing wrapper for navigation function
 */
function createTimingWrapper(
    fn: (url: string, config: NavigateConfig) => void,
    timing: TimingConfig
): (url: string, config: NavigateConfig) => void {
    switch (timing.type) {
        case "debounce":
            return createDebounce(fn, timing.duration, timing.leading);

        case "throttle":
            return createThrottle(fn, timing.duration, timing.leading);

        case "delay":
            return createDelay(fn, timing.duration);

        default:
            return fn;
    }
}

/**
 * Simple debounce implementation
 */
function createDebounce<T extends (...args: any[]) => void>(
    fn: T,
    delay: number,
    leading?: boolean
): T {
    let timeout: ReturnType<typeof setTimeout> | undefined;
    let hasRun = false;

    return ((...args: Parameters<T>) => {
        const callNow = leading && !hasRun;

        clearTimeout(timeout);

        timeout = setTimeout(() => {
            hasRun = false;
            if (!leading) fn(...args);
        }, delay);

        if (callNow) {
            hasRun = true;
            fn(...args);
        }
    }) as T;
}

/**
 * Simple throttle implementation
 */
function createThrottle<T extends (...args: any[]) => void>(
    fn: T,
    limit: number,
    leading?: boolean
): T {
    let inThrottle = false;
    let lastArgs: Parameters<T> | null = null;

    return ((...args: Parameters<T>) => {
        if (!inThrottle) {
            if (leading !== false) {
                fn(...args);
            }
            inThrottle = true;

            setTimeout(() => {
                inThrottle = false;
                if (lastArgs && leading === false) {
                    fn(...lastArgs);
                    lastArgs = null;
                }
            }, limit);
        } else {
            lastArgs = args;
        }
    }) as T;
}

/**
 * Simple delay implementation
 */
function createDelay<T extends (...args: any[]) => void>(
    fn: T,
    delay: number
): T {
    return ((...args: Parameters<T>) => {
        setTimeout(() => fn(...args), delay);
    }) as T;
}

/**
 * Handle navigation with configuration
 */
function handleNavigation(url: string, config: NavigateConfig): void {
    try {
        // Process URL with merge configuration
        const finalUrl = processUrlWithMergeConfig(url, config);

        // Ensure hyperNavigate is available
        if (typeof (window as any).hyperNavigate !== "function") {
            console.error(
                "hyperNavigate not available. Falling back to standard navigation."
            );
            window.location.href = finalUrl;
            return;
        }

        // Use global hyperNavigate
        (window as any).hyperNavigate(finalUrl, config.key);

        // Handle browser history
        if (config.replace) {
            history.replaceState(null, "", finalUrl);
        } else {
            // hyperNavigate already handles pushState, but we ensure consistency
            setTimeout(() => {
                if (window.location.href !== finalUrl) {
                    history.pushState(null, "", finalUrl);
                }
            }, 0);
        }
    } catch (error) {
        console.error("Navigation failed:", error);
        window.location.href = url;
    }
}

/**
 * Process URL with merge configuration - EXPLICIT and PREDICTABLE
 */
function processUrlWithMergeConfig(
    url: string,
    config: NavigateConfig
): string {
    // If no merging, return URL as-is
    if (!config.merge) {
        return url;
    }

    try {
        const currentParams = new URLSearchParams(window.location.search);

        let targetUrl: URL;
        let targetPath: string;
        let targetParams: URLSearchParams;

        // Parse target URL
        if (url.startsWith("?")) {
            targetPath = window.location.pathname;
            targetParams = new URLSearchParams(url);
        } else if (url.startsWith("http")) {
            targetUrl = new URL(url);
            targetPath = targetUrl.pathname;
            targetParams = targetUrl.searchParams;
        } else {
            const baseUrl = new URL(window.location.origin);
            targetUrl = new URL(url, baseUrl);
            targetPath = targetUrl.pathname;
            targetParams = targetUrl.searchParams;
        }

        const mergedParams = new URLSearchParams();

        // Step 1: Add current parameters (filtered by only/except)
        for (const [key, value] of currentParams) {
            const shouldInclude = config.only
                ? config.only.includes(key)
                : !config.except || !config.except.includes(key);

            if (shouldInclude) {
                mergedParams.set(key, value);
            }
        }

        // Step 2: Add/override with target parameters
        for (const [key, value] of targetParams) {
            if (!value || value.trim() === "") {
                // Remove empty parameters
                mergedParams.delete(key);
            } else {
                mergedParams.set(key, value);
            }
        }

        // Step 3: Build final URL
        const queryString = mergedParams.toString();
        return queryString ? `${targetPath}?${queryString}` : targetPath;
    } catch (error) {
        console.warn("Error processing URL merge:", error);
        return url;
    }
}

/**
 * Determine if we should intercept navigation for this link
 */
function shouldInterceptNavigation(link: HTMLAnchorElement): boolean {
    // Skip external links
    try {
        const url = new URL(link.href, window.location.href);
        if (url.origin !== window.location.origin) {
            return false;
        }
    } catch {
        return false;
    }

    // Skip download links
    if (link.hasAttribute("download")) {
        return false;
    }

    // Skip explicitly opted-out links
    if (link.hasAttribute("data-navigate-skip")) {
        return false;
    }

    return true;
}

/**
 * Determine if we should intercept form submission
 */
function shouldInterceptFormSubmit(form: HTMLFormElement): boolean {
    // Only handle GET forms
    if (form.method.toLowerCase() !== "get") {
        return false;
    }

    // Skip explicitly opted-out forms
    if (form.hasAttribute("data-navigate-skip")) {
        return false;
    }

    return true;
}
