// hyper-ext/plugins/backend/actions/fetch-laravel.ts
import type { ActionPlugin } from "../../../../datastar/engine/types";
import {
    createHttpMethod,
    type FetchArgs,
} from "../../../../datastar/plugins/backend/actions/fetch";
import { getCSRFHeaders } from "../../../utils/csrf";

/**
 * Factory for creating Laravel-aware HTTP method plugins with auto-CSRF injection
 * Enhances base Datastar fetch with Laravel CSRF token handling
 */
export const createHttpMethodWithCSRF = (
    name: string,
    method: string
): ActionPlugin => {
    const basePlugin = createHttpMethod(name, method);

    return {
        ...basePlugin,
        fn: async (ctx, url: string, args: FetchArgs) => {
            // Enhance args with CSRF headers
            const enhancedArgs = {
                ...args,
                headers: {
                    ...args?.headers,
                    ...getCSRFHeaders(args?.headers),
                },
            };

            // Delegate to base plugin with enhanced args
            return basePlugin.fn(ctx, url, enhancedArgs);
        },
    };
};
