// Icon: material-symbols:repeat
// Slug: Iterates over an array or object with efficient diffing.
// Description: Production-ready loop directive with keyed diffing, zero signal pollution, and full Datastar reactivity integration.

import type {
    AttributePlugin,
    HTMLOrSVG,
} from "../../../datastar/engine/types";
import { apply } from "../../../datastar/engine/engine";

/**
 * ARCHITECTURE: Clean Loop Implementation
 *
 * PRINCIPLES:
 * 1. NO SIGNAL POLLUTION - Only source signals exist (e.g., $users)
 * 2. Direct mapping: iterator "user" â†’ source path "users.0", "users.1", etc.
 * 3. Datastar's Proxy-based reactivity handles all property mutations
 * 4. Keyed diffing reuses DOM elements, retransforming indices on position changes
 * 5. Efficient change detection: no-change, simple-add, simple-remove, complex
 */

interface IterationState {
    prevKeys: any[];
    lookup: Map<any, LoopElement>;
    template: HTMLTemplateElement;
    templateContent: DocumentFragment;
    iteratorNames: ParsedExpression;
    keyExpression: string | null;
    effectCleanup: (() => void) | null;
    sourceSignalPath: string;
    isNormalized: boolean;
    iterationId: string;
}

interface LoopElement {
    el: HTMLOrSVG;
    key: any;
    index: number;
}

interface ParsedExpression {
    item: string;
    index?: string;
    collection?: string;
    items: string;
}

const iterationStates = new WeakMap<HTMLOrSVG, IterationState>();

export const For: AttributePlugin = {
    type: "attribute",
    name: "for",
    keyReq: "denied",
    valReq: "must",
    shouldEvaluate: false,
    onLoad: (ctx) => {
        const { el, value, mods, effect, getPath, runtimeErr, startBatch, endBatch, peek } = ctx;

        // Validate template element
        if (!(el instanceof HTMLTemplateElement)) {
            throw runtimeErr("ForMustBeOnTemplate", {
                message: "data-for must be used on <template> elements",
            });
        }

        // Parse expression: "user in $users", "[name, email] in $users", "item, index in $items"
        const parsed = parseExpression(value);
        if (!parsed) {
            throw runtimeErr("InvalidForExpression", {
                expression: value,
                expected: 'Format: "item in $items", "[name, age] in $users", "item, index in $items"',
            });
        }

        // Get key expression from data-for__key modifier
        const keyMod = mods.get("key");
        const keyExpression = keyMod && keyMod.size > 0 ? Array.from(keyMod)[0] : null;

        // Setup
        const template = el as HTMLTemplateElement;
        template.style.display = "none";
        const templateContent = template.content.cloneNode(true) as DocumentFragment;

        const sourceSignalPath = parsed.items;
        const sourceData = getPath(sourceSignalPath);
        const isNormalized = shouldNormalizeData(sourceData);

        // Generate unique iteration ID for this data-for instance
        const iterationId = `__for_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

        const state: IterationState = {
            prevKeys: [],
            lookup: new Map(),
            template,
            templateContent,
            iteratorNames: parsed,
            keyExpression,
            effectCleanup: null,
            sourceSignalPath,
            isNormalized,
            iterationId,
        };

        iterationStates.set(el, state);

        // Create reactive effect that tracks source signal
        const effectCleanup = effect(() => {
            // Access source signal reactively through getPath
            // This properly triggers Datastar's reactive tracking
            const sourceData = getPath(sourceSignalPath);

            // For arrays, track structure changes AND deep properties (for objects only)
            if (Array.isArray(sourceData)) {
                const reactiveArray = ctx.root[sourceSignalPath];

                // Track deep properties ONLY for object items (not primitives)
                // This enables reactive updates like: $users[0].name = 'New Name'
                if (reactiveArray && reactiveArray.length > 0) {
                    const firstItem = reactiveArray[0];
                    // Only track deep if items are objects (not strings/numbers)
                    if (firstItem && typeof firstItem === 'object' && !Array.isArray(firstItem)) {
                        for (const key in reactiveArray) {
                            const item = reactiveArray[key];
                            void item; // Track array structure
                            // Track all object properties for reactivity
                            if (item && typeof item === 'object') {
                                for (const prop in item) {
                                    void item[prop];
                                }
                            }
                        }
                    }
                }
            }

            const items = normalizeData(sourceData);

            const newArrayWithKeys = items.map((item, index) => ({
                item,
                index,
                key: evaluateKey(keyExpression, item, index),
            }));

            // Prevent tracking during DOM updates (critical for performance)
            peek(() => {
                startBatch();
                try {
                    diffAndUpdate(newArrayWithKeys, state, ctx);
                } finally {
                    endBatch();
                }
            });
        });

        state.effectCleanup = effectCleanup;

        return () => {
            cleanup(state);
            iterationStates.delete(el);
        };
    },
};

/**
 * Parse for expression
 */
function parseExpression(expression: string): ParsedExpression | null {
    const forIteratorRE = /,([^,\}\]]*)(?:,([^,\}\]]*))?$/;
    const stripParensRE = /^\s*\(|\)\s*$/g;
    const forAliasRE = /([\s\S]*?)\s+(?:in|of)\s+([\s\S]*)/;

    const match = expression.match(forAliasRE);
    if (!match) return null;

    let itemsExpr = match[2].trim();
    if (itemsExpr.startsWith("$")) {
        itemsExpr = itemsExpr.slice(1);
    }

    const result: ParsedExpression = {
        item: "",
        items: itemsExpr,
    };

    let item = match[1].replace(stripParensRE, "").trim();
    const iteratorMatch = item.match(forIteratorRE);

    if (iteratorMatch) {
        result.item = item.replace(forIteratorRE, "").trim();
        result.index = iteratorMatch[1].trim();
        if (iteratorMatch[2]) {
            result.collection = iteratorMatch[2].trim();
        }
    } else {
        result.item = item;
    }

    return result;
}

/**
 * Diff and update DOM with keyed diffing
 */
function diffAndUpdate(
    newArrayWithKeys: Array<{ item: any; index: number; key: any }>,
    state: IterationState,
    ctx: any
) {
    // Handle duplicate keys
    const seen = new Set();
    const finalArray = newArrayWithKeys.map((data, i) => {
        let finalKey = data.key;
        if (seen.has(finalKey)) {
            finalKey = `${finalKey}_dup_${i}`;
        }
        seen.add(finalKey);
        return { ...data, key: finalKey };
    });

    const newKeys = finalArray.map(d => d.key);
    const prevKeys = state.prevKeys;

    // Detect change type
    const changeType = detectChange(prevKeys, newKeys);

    if (changeType === 'no-change') {
        // NO-CHANGE: Keys haven't changed, but properties might have
        // Sync ephemeral signals from source for DISPLAY properties (data-text, etc.)
        // Bindings (data-bind) update ephemeral signals directly, but display
        // properties need to be synced when source properties change
        if (!state.isNormalized && finalArray.length > 0) {
            // Check if this is an object array (first item is an object)
            const firstItem = finalArray[0].item;
            if (firstItem && typeof firstItem === 'object' && !Array.isArray(firstItem)) {
                for (const itemData of finalArray) {
                    const loopEl = state.lookup.get(itemData.key);
                    if (loopEl) {
                        const sanitizedKey = sanitizeKey(itemData.key);
                        // Sync ephemeral signal with latest source data
                        ctx.mergePatch({
                            [state.iterationId]: {
                                [sanitizedKey]: itemData.item
                            }
                        });
                    }
                }
            }
        }
        return;
    } else if (changeType === 'simple-add') {
        handleAdd(finalArray, state, ctx);
    } else if (changeType === 'simple-remove') {
        handleRemove(prevKeys, newKeys, state, ctx, finalArray);
    } else {
        handleReorder(finalArray, state, ctx);
    }

    state.prevKeys = [...newKeys];
}

/**
 * Detect change type for optimized updates
 */
function detectChange(prevKeys: any[], newKeys: any[]): 'no-change' | 'simple-add' | 'simple-remove' | 'complex' {
    if (prevKeys.length === newKeys.length && prevKeys.every((k, i) => k === newKeys[i])) {
        return 'no-change';
    }

    if (prevKeys.length < newKeys.length) {
        if (prevKeys.every((k, i) => k === newKeys[i])) {
            return 'simple-add';
        }
    }

    if (prevKeys.length > newKeys.length) {
        const newSet = new Set(newKeys);
        if (!newKeys.some(k => !new Set(prevKeys).has(k))) {
            let newIdx = 0;
            for (const pk of prevKeys) {
                if (newSet.has(pk)) {
                    if (newKeys[newIdx] !== pk) return 'complex';
                    newIdx++;
                }
            }
            return 'simple-remove';
        }
    }

    return 'complex';
}

/**
 * Handle simple additions
 */
function handleAdd(
    data: Array<{ item: any; index: number; key: any }>,
    state: IterationState,
    ctx: any
) {
    const startIdx = state.prevKeys.length;
    let prevEl: HTMLOrSVG = state.template;

    if (startIdx > 0) {
        const lastKey = state.prevKeys[startIdx - 1];
        const last = state.lookup.get(lastKey);
        if (last) prevEl = last.el;
    }

    for (let i = startIdx; i < data.length; i++) {
        const item = data[i];
        const loopEl = createElement(item, state, ctx);
        state.lookup.set(item.key, loopEl);
        prevEl.after(loopEl.el);
        prevEl = loopEl.el;
        queueMicrotask(() => apply(loopEl.el));
    }
}

/**
 * Handle simple removals
 */
function handleRemove(
    prevKeys: any[],
    newKeys: any[],
    state: IterationState,
    ctx: any,
    data: Array<{ item: any; index: number; key: any }>
) {
    const newSet = new Set(newKeys);

    // Remove elements no longer in new keys
    for (const key of prevKeys) {
        if (!newSet.has(key)) {
            const loopEl = state.lookup.get(key);
            if (loopEl) {
                loopEl.el.remove();
                state.lookup.delete(key);
            }
        }
    }

    // Update indices for remaining elements
    for (const itemData of data) {
        const loopEl = state.lookup.get(itemData.key);
        if (loopEl && loopEl.index !== itemData.index) {
            loopEl.index = itemData.index;

            // Update index in ephemeral signal for reactive updates
            if (!state.isNormalized) {
                const sanitizedKey = sanitizeKey(itemData.key);
                const currentData = ctx.root[state.iterationId]?.[sanitizedKey];
                if (currentData !== undefined) {
                    ctx.mergePatch({
                        [state.iterationId]: {
                            [`${sanitizedKey}__index`]: itemData.index
                        }
                    });
                }
            }
        }
    }
}

/**
 * Handle complex changes with smart reordering
 */
function handleReorder(
    data: Array<{ item: any; index: number; key: any }>,
    state: IterationState,
    ctx: any
) {
    const newSet = new Set(data.map(d => d.key));

    // Remove elements no longer in new keys
    for (const pk of state.prevKeys) {
        if (!newSet.has(pk)) {
            const loopEl = state.lookup.get(pk);
            if (loopEl) {
                loopEl.el.remove();
                state.lookup.delete(pk);
            }
        }
    }

    // Reorder/create elements
    let prevEl: HTMLOrSVG = state.template;
    for (const item of data) {
        const existing = state.lookup.get(item.key);

        if (existing) {
            // Reuse element - update index in ephemeral signal if it changed
            if (existing.index !== item.index) {
                existing.index = item.index;

                // Update index in ephemeral signal for reactive updates
                if (!state.isNormalized) {
                    const sanitizedKey = sanitizeKey(item.key);
                    const currentData = ctx.root[state.iterationId]?.[sanitizedKey];
                    if (currentData !== undefined) {
                        ctx.mergePatch({
                            [state.iterationId]: {
                                [`${sanitizedKey}__index`]: item.index
                            }
                        });
                    }
                }
            }

            if (existing.el.previousElementSibling !== prevEl) {
                prevEl.after(existing.el);
            }
            prevEl = existing.el;
        } else {
            // Create new element
            const loopEl = createElement(item, state, ctx);
            state.lookup.set(item.key, loopEl);
            prevEl.after(loopEl.el);
            prevEl = loopEl.el;
            queueMicrotask(() => apply(loopEl.el));
        }
    }
}

/**
 * Create new loop element with EPHEMERAL SIGNALS
 *
 * CRITICAL: Use ephemeral signals (keyed by item key) to prevent source signal corruption
 */
function createElement(
    itemData: { item: any; index: number; key: any },
    state: IterationState,
    ctx: any
): LoopElement {
    const { templateContent, sourceSignalPath, iteratorNames, isNormalized, iterationId } = state;

    const clone = templateContent.cloneNode(true) as DocumentFragment;
    const el = clone.firstElementChild as HTMLOrSVG;

    // Map iterator to ephemeral signal path (keyed by item key to prevent corruption)
    let signalPath = sourceSignalPath;
    let indexSignalPath: string | null = null;
    if (!isNormalized) {
        // For arrays: Create ephemeral signal keyed by unique key
        // This prevents corruption of source signal
        const sanitizedKey = sanitizeKey(itemData.key);
        signalPath = `${iterationId}.${sanitizedKey}`;
        indexSignalPath = `${iterationId}.${sanitizedKey}__index`;

        // Create ephemeral signals for item and index
        ctx.mergePatch({
            [iterationId]: {
                [sanitizedKey]: itemData.item,
                [`${sanitizedKey}__index`]: itemData.index
            }
        });
    }

    transformElement(el, iteratorNames, itemData.index, signalPath, indexSignalPath, isNormalized, itemData.item);

    return {
        el,
        key: itemData.key,
        index: itemData.index,
    };
}

/**
 * Sanitize key for use as signal path
 */
function sanitizeKey(key: any): string {
    return String(key).replace(/[^a-zA-Z0-9_]/g, '_');
}

/**
 * Transform element attributes
 */
function transformElement(
    el: HTMLOrSVG,
    iterators: ParsedExpression,
    index: number,
    signalPath: string,
    indexSignalPath: string | null,
    isNormalized: boolean,
    value: any
) {
    const process = (elem: Element): void => {
        Array.from(elem.attributes).forEach((attr) => {
            if (attr.name.startsWith("data-") && attr.value) {
                attr.value = transformExpression(
                    attr.value,
                    attr.name,
                    iterators,
                    index,
                    signalPath,
                    indexSignalPath,
                    isNormalized,
                    value
                );
            }
        });

        if (elem instanceof HTMLTemplateElement && elem.content) {
            Array.from(elem.content.children).forEach(child => process(child));
        } else {
            Array.from(elem.children).forEach(child => process(child));
        }
    };

    process(el);
}

/**
 * Transform expression
 */
function transformExpression(
    expr: string,
    attrName: string,
    iterators: ParsedExpression,
    index: number,
    signalPath: string,
    indexSignalPath: string | null,
    isNormalized: boolean,
    value: any
): string {
    let result = expr;

    // Determine if signal name or expression
    const isSignalName = /^(bind|ref|indicator|signals|computed)/.test(attrName.replace(/^data-/, ""));

    // Protect string literals
    const literals: string[] = [];
    result = result
        .replace(/'(?:[^'\\]|\\.)*'/g, m => (literals.push(m), `__LIT${literals.length - 1}__`))
        .replace(/"(?:[^"\\]|\\.)*"/g, m => (literals.push(m), `__LIT${literals.length - 1}__`))
        .replace(/`(?:[^`\\]|\\.)*`/g, m => (literals.push(m), `__LIT${literals.length - 1}__`));

    // Transform index - use reactive signal if available, otherwise literal
    if (iterators.index) {
        if (indexSignalPath && !isNormalized) {
            // Use reactive index signal with $ prefix for expressions
            const prefix = isSignalName ? "" : "$";
            result = result.replace(new RegExp(`\\b${esc(iterators.index)}\\b`, "g"), prefix + indexSignalPath);
        } else {
            // Fallback to literal index for normalized data
            result = result.replace(new RegExp(`\\b${esc(iterators.index)}\\b`, "g"), index.toString());
        }
    }

    // Transform collection
    if (iterators.collection) {
        const prefix = isSignalName ? "" : "$";
        result = result.replace(new RegExp(`\\b${esc(iterators.collection)}\\b`, "g"), prefix + signalPath);
    }

    // Transform item
    if (isDestructured(iterators.item)) {
        const vars = extractVars(iterators.item);
        const isArrayDestructuring = iterators.item.trim().startsWith('[');

        vars.forEach((v, idx) => {
            let varSignalPath: string;
            let varValue: any;

            if (isArrayDestructuring) {
                // Array destructuring: [x, y] â†’ signalPath.0, signalPath.1
                varSignalPath = `${signalPath}.${idx}`;
                varValue = Array.isArray(value) ? value[idx] : value;
            } else {
                // Object destructuring: {name, price} â†’ signalPath.name, signalPath.price
                varSignalPath = `${signalPath}.${v}`;
                varValue = value && typeof value === 'object' ? value[v] : value;
            }

            result = transformVar(result, v, varSignalPath, isSignalName, isNormalized, varValue, iterators);
        });
    } else {
        result = transformVar(result, iterators.item, signalPath, isSignalName, isNormalized, value, iterators);
    }

    // Restore literals
    result = result.replace(/__LIT(\d+)__/g, (_, i) => literals[parseInt(i)]);

    return result;
}

/**
 * Transform variable
 */
function transformVar(
    expr: string,
    varName: string,
    signalPath: string,
    isSignalName: boolean,
    isNormalized: boolean,
    value: any,
    _iterators: ParsedExpression
): string {
    if (isNormalized) {
        // Value-based: replace with literal value
        return expr.replace(new RegExp(`\\b${esc(varName)}\\b(?!\\.)`, "g"), JSON.stringify(value));
    }

    const prefix = isSignalName ? "" : "$";

    // Property access: varName.prop â†’ signalPath.prop
    expr = expr.replace(
        new RegExp(`\\b${esc(varName)}\\.([a-zA-Z_][a-zA-Z0-9_.]*)\\b`, "g"),
        `${prefix}${signalPath}.$1`
    );

    // Standalone: varName â†’ signalPath
    expr = expr.replace(
        new RegExp(`\\b${esc(varName)}\\b(?!\\.)`, "g"),
        prefix + signalPath
    );

    return expr;
}

/**
 * Utilities
 */
function shouldNormalizeData(data: any): boolean {
    return typeof data === "number" || (data && typeof data === "object" && !Array.isArray(data));
}

function normalizeData(data: any): any[] {
    if (data == null) return [];
    if (Array.isArray(data)) return data;
    if (typeof data === "number") return Array.from({ length: data }, (_, i) => i + 1);
    if (typeof data === "object") return Object.entries(data);
    return [data];
}

function evaluateKey(keyExpr: string | null, item: any, index: number): any {
    if (!keyExpr) {
        if (item && typeof item === "object") {
            if ("id" in item && item.id != null) return item.id;
            if ("uuid" in item && item.uuid != null) return item.uuid;
            if ("key" in item && item.key != null) return item.key;
        }
        return index;
    }

    if (keyExpr === "index") return index;

    if (keyExpr.includes('.')) {
        const parts = keyExpr.split('.');
        let val = item;
        for (const part of parts) {
            if (val && typeof val === "object" && part in val) {
                val = val[part];
            } else {
                return index;
            }
        }
        return val !== undefined && val !== null ? val : index;
    }

    if (item && typeof item === "object" && keyExpr in item) {
        const keyVal = item[keyExpr];
        return keyVal !== undefined && keyVal !== null ? keyVal : index;
    }

    return index;
}

function isDestructured(item: string): boolean {
    return /^\[.*\]$/.test(item) || /^\{.*\}$/.test(item);
}

function extractVars(item: string): string[] {
    return item
        .replace(/[\[\]\{\}]/g, "")
        .split(",")
        .map(i => i.trim());
}

function esc(str: string): string {
    return str.replace(/[.*+?^${}()|[\]\\]/g, "\\$&");
}

function cleanup(state: IterationState) {
    if (state.effectCleanup) {
        state.effectCleanup();
        state.effectCleanup = null;
    }

    state.lookup.forEach(loopEl => loopEl.el.remove());
    state.lookup.clear();
    state.prevKeys.length = 0;
}
