// packages/dancycodes/hyper-front/src/hyper-ext/plugins/backend/actions/navigate.ts
// Icon: material-symbols:navigation
// Slug: Enhanced navigate action with JSON query support and consistent behavior.
// Description: Professional navigate action that supports both string URLs and JSON query objects.

import type {
    ActionPlugin,
    RuntimeContext,
} from "../../../../datastar/engine/types";

/**
 * Enhanced Navigate Action - Professional Navigation with JSON Support
 *
 * Provides consistent navigation behavior between frontend expressions and backend responses.
 * Supports both traditional URL strings and JSON query parameter objects.
 *
 * USAGE EXAMPLES:
 *
 * Traditional URL Navigation:
 * @navigate('/dashboard')                              // Basic navigation
 * @navigate('/users', 'main')                         // Navigation with key
 * @navigate('/contacts?search=john', 'filters')       // URL with query params
 *
 * JSON Query Navigation (NEW):
 * @navigate({search: 'john', page: 1}, 'filters')                    // JSON queries
 * @navigate({search: 'john', page: 1}, 'filters', {merge: true})     // JSON with merge
 * @navigate({}, 'clear')                                             // Clear all queries
 *
 * Advanced Options:
 * @navigate('/users', 'main', {merge: false})                       // Explicit no merge
 * @navigate('/users', 'main', {only: ['search', 'category']})       // Only preserve specific
 * @navigate('/users', 'main', {except: ['page']})                   // Preserve except specific
 * @navigate('/users', 'main', {replace: true})                      // Replace instead of push
 *
 * Mixed Usage:
 * @navigate('/contacts', 'main', {queries: {search: $searchTerm}, merge: true})
 */
export const NavigateAction: ActionPlugin = {
    type: "action",
    name: "navigate",
    fn: (
        ctx: RuntimeContext,
        urlOrQueries: string | Record<string, any>,
        key: string = "true",
        options: NavigateOptions = {}
    ): void => {
        // Input validation
        if (!urlOrQueries) {
            throw ctx.runtimeErr("NavigateUrlRequired", {
                received: String(urlOrQueries),
            });
        }

        if (typeof key !== "string") {
            throw ctx.runtimeErr("NavigateKeyMustBeString", {
                received: String(key),
            });
        }

        try {
            // Process the URL based on input type
            const finalUrl = processNavigationInput(urlOrQueries, options);

            // Ensure hyperNavigate is available
            if (typeof (window as any).hyperNavigate !== "function") {
                console.error(
                    "hyperNavigate is not available. Ensure GlobalNavigate watcher is loaded."
                );
                window.location.href = finalUrl;
                return;
            }

            // Execute navigation
            (window as any).hyperNavigate(finalUrl, key);

            // Handle browser history if specified
            if (options.replace) {
                setTimeout(() => {
                    history.replaceState(null, "", finalUrl);
                }, 0);
            }
        } catch (error) {
            console.error("Navigate action failed:", error);

            // Fallback to standard navigation
            const fallbackUrl =
                typeof urlOrQueries === "string"
                    ? urlOrQueries
                    : `${window.location.pathname}?${buildQueryString(
                          urlOrQueries
                      )}`;
            window.location.href = fallbackUrl;
        }
    },
};

/**
 * Navigation options interface - matches both frontend and backend
 */
interface NavigateOptions {
    merge?: boolean; // Whether to merge with existing query parameters
    only?: string[]; // Only preserve these query parameters
    except?: string[]; // Preserve all query parameters except these
    replace?: boolean; // Use replaceState instead of pushState
    queries?: Record<string, any>; // Additional queries to merge (when urlOrQueries is a path)
}

/**
 * Process navigation input based on type and options
 */
function processNavigationInput(
    urlOrQueries: string | Record<string, any>,
    options: NavigateOptions
): string {
    if (typeof urlOrQueries === "string") {
        // Traditional string URL
        return processStringUrl(urlOrQueries, options);
    } else {
        // JSON query object
        return processJsonQueries(urlOrQueries, options);
    }
}

/**
 * Process traditional string URL with options
 */
function processStringUrl(url: string, options: NavigateOptions): string {
    // Handle additional queries option
    if (options.queries && Object.keys(options.queries).length > 0) {
        url = mergeQueriesIntoUrl(url, options.queries);
    }

    // Apply merge logic if specified
    if (shouldApplyMerge(options)) {
        return mergeQueryParameters(url, options.only, options.except);
    }

    return url;
}

/**
 * Process JSON query object into URL
 */
function processJsonQueries(
    queries: Record<string, any>,
    options: NavigateOptions
): string {
    // Build base URL from current location
    const currentPath = window.location.pathname;
    const queryString = buildQueryString(queries);

    const baseUrl = queryString ? `${currentPath}?${queryString}` : currentPath;

    // Apply merge logic if specified
    if (shouldApplyMerge(options)) {
        return mergeQueryParameters(baseUrl, options.only, options.except);
    }

    return baseUrl;
}

/**
 * Determine if merge logic should be applied
 */
function shouldApplyMerge(options: NavigateOptions): boolean {
    // Explicit merge setting takes precedence
    if (options.merge !== undefined) {
        return options.merge;
    }

    // Implied merge when using only/except
    if (options.only || options.except) {
        return true;
    }

    // Default: no merge (explicit behavior)
    return false;
}

/**
 * Build query string from object, handling various value types
 */
function buildQueryString(queries: Record<string, any>): string {
    const params = new URLSearchParams();

    for (const [key, value] of Object.entries(queries)) {
        if (value === null || value === undefined) {
            // Skip null/undefined values
            continue;
        }

        if (Array.isArray(value)) {
            // Handle arrays (for multi-select, checkboxes, etc.)
            value.forEach((item) => {
                if (item !== null && item !== undefined && item !== "") {
                    params.append(key, String(item));
                }
            });
        } else if (value === "" || String(value).trim() === "") {
            // Skip empty strings
            continue;
        } else {
            // Handle primitive values
            params.set(key, String(value));
        }
    }

    return params.toString();
}

/**
 * Merge additional queries into existing URL
 */
function mergeQueriesIntoUrl(
    url: string,
    additionalQueries: Record<string, any>
): string {
    try {
        let targetUrl: URL;

        if (url.startsWith("?")) {
            targetUrl = new URL(
                `${window.location.pathname}${url}`,
                window.location.origin
            );
        } else if (url.startsWith("http")) {
            targetUrl = new URL(url);
        } else {
            targetUrl = new URL(url, window.location.origin);
        }

        // Add additional queries
        for (const [key, value] of Object.entries(additionalQueries)) {
            if (value === null || value === undefined || value === "") {
                targetUrl.searchParams.delete(key);
            } else if (Array.isArray(value)) {
                targetUrl.searchParams.delete(key);
                value.forEach((item) => {
                    if (item !== null && item !== undefined && item !== "") {
                        targetUrl.searchParams.append(key, String(item));
                    }
                });
            } else {
                targetUrl.searchParams.set(key, String(value));
            }
        }

        return `${targetUrl.pathname}${targetUrl.search}`;
    } catch (error) {
        console.warn("Error merging queries into URL:", error);
        return url;
    }
}

/**
 * Merge query parameters with current URL - consistent with attribute plugin
 */
function mergeQueryParameters(
    url: string,
    only?: string[],
    except?: string[]
): string {
    try {
        const currentParams = new URLSearchParams(window.location.search);

        let targetUrl: URL;
        let targetPath: string;
        let targetParams: URLSearchParams;

        // Parse target URL (same logic as attribute plugin)
        if (url.startsWith("?")) {
            targetPath = window.location.pathname;
            targetParams = new URLSearchParams(url);
        } else if (url.startsWith("http")) {
            targetUrl = new URL(url);
            targetPath = targetUrl.pathname;
            targetParams = targetUrl.searchParams;
        } else {
            const baseUrl = new URL(window.location.origin);
            targetUrl = new URL(url, baseUrl);
            targetPath = targetUrl.pathname;
            targetParams = targetUrl.searchParams;
        }

        const mergedParams = new URLSearchParams();

        // Step 1: Add current parameters (filtered by only/except)
        for (const [key, value] of currentParams) {
            const shouldInclude = only
                ? only.includes(key)
                : !except || !except.includes(key);

            if (shouldInclude) {
                mergedParams.set(key, value);
            }
        }

        // Step 2: Add/override with target parameters
        for (const [key, value] of targetParams) {
            if (!value || value.trim() === "") {
                mergedParams.delete(key);
            } else {
                mergedParams.set(key, value);
            }
        }

        // Step 3: Build final URL
        const queryString = mergedParams.toString();
        return queryString ? `${targetPath}?${queryString}` : targetPath;
    } catch (error) {
        console.warn("Error merging query parameters:", error);
        return url;
    }
}

/**
 * EXAMPLES FOR DOCUMENTATION:
 *
 * // Traditional string navigation
 * @navigate('/users')                                    // Clean navigation
 * @navigate('/users?active=true', 'main')               // With queries
 *
 * // JSON query navigation
 * @navigate({search: $searchTerm, page: 1})             // Pure queries
 * @navigate({category: 'work'}, 'filters')              // With key
 *
 * // Advanced options
 * @navigate('/users', 'main', {
 *   queries: {search: $searchTerm},
 *   merge: true,
 *   except: ['page']
 * })
 *
 * // Clear all queries
 * @navigate({})                                         // Clear everything
 * @navigate('/')                                        // Clean slate
 *
 * // Array handling
 * @navigate({tags: ['php', 'laravel'], status: 'active'})  // Multi-value params
 */
