/**
 * Laravel CSRF Token Utilities
 * Handles automatic CSRF token injection for Laravel routes
 */

/**
 * Get CSRF token from Laravel's meta tag
 */
export function getCSRFToken(): string | null {
    const metaTag = document.querySelector('meta[name="csrf-token"]');
    return metaTag?.getAttribute("content") || null;
}

/**
 * Check if user already provided CSRF token manually
 */
export function hasManualCSRF(
    userHeaders: Record<string, string> = {}
): boolean {
    const csrfHeaders = ["X-CSRF-TOKEN", "x-csrf-token", "X-Csrf-Token"];
    return csrfHeaders.some((header) => userHeaders[header]);
}

/**
 * Get CSRF headers for Laravel routes (only if not manually provided)
 */
export function getCSRFHeaders(
    userHeaders: Record<string, string> = {}
): Record<string, string> {
    if (hasManualCSRF(userHeaders)) {
        return {}; // Don't override manual CSRF
    }

    const csrfToken = getCSRFToken();
    return csrfToken ? { "X-CSRF-TOKEN": csrfToken } : {};
}
