// Icon: material-symbols:send
// Slug: Dispatches custom browser events for component communication.
// Description: Dispatches CustomEvents globally or to specific elements, enabling event-driven reactive patterns like Livewire.

import type { ActionPlugin } from '../../../datastar/engine/types';

/**
 * Dispatch Action - Event Communication System
 *
 * Mirrors Livewire's dispatch system, enabling both frontend and backend
 * to trigger custom events for component communication.
 *
 * Usage:
 * @dispatch('event-name')                                  // Global dispatch
 * @dispatch('event-name', {data: 'value'})                // With data
 * @dispatch('event-name', {id: 123}, {selector: '#target'}) // Targeted
 *
 * Options:
 * - selector: CSS selector for targeted dispatch (multiple elements supported)
 * - window: Force window dispatch (default: true if no selector)
 * - bubbles: Event bubbles (default: true)
 * - cancelable: Event can be canceled (default: true)
 * - composed: Event composes through shadow DOM (default: true)
 */
export const Dispatch: ActionPlugin = {
    type: 'action',
    name: 'dispatch',
    fn: (
        { el },
        eventName: string,
        data?: any,
        options?: {
            selector?: string;
            window?: boolean;
            bubbles?: boolean;
            cancelable?: boolean;
            composed?: boolean;
        }
    ) => {
        // Validate event name
        if (!eventName || typeof eventName !== 'string') {
            console.error(
                '[Hyper Dispatch] Invalid event name. Must be a non-empty string.',
                { eventName }
            );
            return;
        }

        // Default options
        const opts = {
            selector: options?.selector,
            window: options?.window ?? (!options?.selector), // Default to window if no selector
            bubbles: options?.bubbles ?? true,
            cancelable: options?.cancelable ?? true,
            composed: options?.composed ?? true,
        };

        // Create CustomEvent
        const event = new CustomEvent(eventName, {
            detail: data || {},
            bubbles: opts.bubbles,
            cancelable: opts.cancelable,
            composed: opts.composed,
        });

        // Targeted dispatch
        if (opts.selector) {
            const targets = document.querySelectorAll(opts.selector);

            if (targets.length === 0) {
                console.warn(
                    `[Hyper Dispatch] No elements found for selector: ${opts.selector}`,
                    { eventName, selector: opts.selector }
                );
                return;
            }

            targets.forEach((target) => {
                target.dispatchEvent(new CustomEvent(eventName, {
                    detail: data || {},
                    bubbles: opts.bubbles,
                    cancelable: opts.cancelable,
                    composed: opts.composed,
                }));
            });

            return;
        }

        // Global dispatch (window)
        if (opts.window) {
            window.dispatchEvent(event);
            return;
        }

        // Element dispatch (fallback to current element)
        el.dispatchEvent(event);
    },
};
