// hyper-front/src/hyper-ext/plugins/backend/watchers/popstateHandler.ts
// Icon: material-symbols:history
// Slug: Handles browser back/forward navigation intelligently.
// Description: Smart popstate handler that respects navigation state and opt-in behavior.

import type { WatcherPlugin } from "../../../../datastar/engine/types";

// Global flag OUTSIDE the function - proper scope!
let popstateInitialized = false;

export const PopstateHandler: WatcherPlugin = {
    type: "watcher",
    name: "popstateHandler",
    onGlobalInit: () => {
        // Only initialize once globally
        if (!popstateInitialized) {
            popstateInitialized = true;

            window.addEventListener("popstate", function (event) {
                // Check if we have any elements with data-navigate on the page
                const hasNavigateElements =
                    document.querySelector("[data-navigate]");

                // If no navigation elements exist, just reload (original behavior)
                if (!hasNavigateElements) {
                    window.location.reload();
                    return;
                }

                // If we have navigation elements, try reactive navigation
                // Check if hyperNavigate is available (it should be via GlobalNavigate watcher)
                if (typeof (window as any).hyperNavigate === "function") {
                    // Determine appropriate navigation key based on state or default
                    const navigationKey =
                        event.state?.navigationKey || "popstate";
                    (window as any).hyperNavigate(
                        window.location.href,
                        navigationKey
                    );
                } else {
                    // Fallback to reload if hyperNavigate isn't available yet
                    window.location.reload();
                }
            });
        }
    },
};
