// packages/dancycodes/hyper-front/src/hyper-ext/plugins/backend/watchers/responseInterceptor.ts
// Icon: material-symbols:intercept
// Slug: Essential Laravel behavior handling - production ready.
// Description: Handles redirects, exceptions, dd/dump with proper Hyper response detection.

import type { WatcherPlugin } from "../../../../datastar/engine/types";

export const ResponseInterceptor: WatcherPlugin = {
    type: "watcher",
    name: "responseInterceptor",
    onGlobalInit: () => {
        const originalFetch = window.fetch;

        window.fetch = async (...args) => {
            const [resource, init] = args;
            const requestUrl = getUrlFromResource(resource);

            // Skip intercepting certain URLs to prevent infinite loops
            if (shouldSkipUrl(requestUrl)) {
                return originalFetch(...args);
            }

            // Check if this is a Datastar request
            const isDatastarRequest =
                (init?.headers as Record<string, string>)?.[
                    "Datastar-Request"
                ] === "true" ||
                (init?.headers as Headers)?.get?.("Datastar-Request") ===
                    "true";

            try {
                const response = await originalFetch(...args);

                // CRITICAL: Check for Laravel redirects FIRST, before any other detection
                if (isRedirectResponse(response, requestUrl)) {
                    const redirectUrl = getRedirectUrl(response);
                    if (redirectUrl && redirectUrl !== window.location.href) {
                        window.location.href = redirectUrl; // Full page redirect
                        return new Response("", { status: 204 });
                    }
                }

                // Always let Datastar requests pass through
                if (isDatastarRequest) {
                    return response;
                }

                // Detect Hyper responses
                const isHyperResponse =
                    response.headers.get("X-Hyper-Response") === "true";
                const isSSEResponse = response.headers
                    .get("Content-Type")
                    ?.includes("text/event-stream");
                const hasDatastarEvent =
                    (response.headers
                        .get("Content-Type")
                        ?.includes("text/event-stream") &&
                        response.url.includes("datastar")) ||
                    response.headers.get("event")?.startsWith("datastar-");

                // Let Datastar handle its own responses
                if (isHyperResponse || isSSEResponse || hasDatastarEvent) {
                    return response;
                }

                // Handle Laravel responses (errors, dd/dump, etc.)
                try {
                    await handleLaravelResponse(response, requestUrl);
                } catch (error) {
                    if (error instanceof RedirectHandled) {
                        return new Response("", { status: 204 });
                    }
                    throw error;
                }

                return new Response("", { status: 200 });
            } catch (error) {
                if (!(error instanceof RedirectHandled)) {
                    console.error("Network error:", error);
                }
                throw error;
            }
        };
    },
};

// Custom error class for cleaner flow control
class RedirectHandled extends Error {
    constructor() {
        super("Redirect handled");
        this.name = "RedirectHandled";
    }
}

function getUrlFromResource(resource: RequestInfo | URL): string {
    if (typeof resource === "string") return resource;
    if (resource instanceof URL) return resource.href;
    if (resource instanceof Request) return resource.url;
    return String(resource);
}

function shouldSkipUrl(url: string): boolean {
    // Skip Laravel Boost, debugging tools, and other internal requests
    const skipPatterns = [
        "_boost/browser-logs",
        "_boost/",
        "_debugbar/",
        "_ignition/",
        "telescope/",
        "horizon/",
        ".js",
        ".css",
        ".png",
        ".jpg",
        ".jpeg",
        ".gif",
        ".svg",
        ".ico",
        ".woff",
        ".woff2",
        ".ttf",
        ".eot",
    ];

    return skipPatterns.some((pattern) => url.includes(pattern));
}

async function handleLaravelResponse(
    response: Response,
    requestUrl: string
): Promise<void> {
    // Handle redirects first
    if (isRedirectResponse(response, requestUrl)) {
        const redirectUrl = getRedirectUrl(response);
        if (redirectUrl && redirectUrl !== window.location.href) {
            window.location.replace(redirectUrl);
            throw new RedirectHandled();
        }
        return;
    }

    // Handle error responses and dd/dump
    if (response.status >= 400) {
        const html = await response.text();
        if (isLaravelSpecialResponse(html)) {
            replaceDocument(html);
        }
        return;
    }

    // Handle successful HTML responses
    const contentType = response.headers.get("Content-Type") || "";
    if (contentType.includes("text/html")) {
        const html = await response.text();
        if (isLaravelSpecialResponse(html)) {
            replaceDocument(html);
        }
    }
}

function isRedirectResponse(response: Response, originalUrl: string): boolean {
    return (
        [301, 302, 303, 307, 308].includes(response.status) ||
        (response.url && response.url !== originalUrl) ||
        !!response.headers.get("Location")
    );
}

function getRedirectUrl(response: Response): string | null {
    let redirectUrl = response.url || response.headers.get("Location");

    if (!redirectUrl) return null;

    // Make relative URLs absolute
    if (redirectUrl.startsWith("/")) {
        redirectUrl = window.location.origin + redirectUrl;
    }

    return redirectUrl;
}

function isLaravelSpecialResponse(html: string): boolean {
    // Laravel error pages (Ignition, Whoops)
    if (
        html.includes("ignition-") ||
        html.includes("data-ignition") ||
        html.includes("Whoops\\") ||
        html.includes("whoops-container") ||
        (html.includes("Illuminate\\") && html.includes("Exception"))
    ) {
        return true;
    }

    // Laravel dd/dump responses
    if (
        html.includes("sf-dump") ||
        html.includes("var-dump") ||
        html.includes("symfony-var-dumper") ||
        html.includes("dump-container")
    ) {
        return true;
    }

    return false;
}

function replaceDocument(html: string): void {
    try {
        // Modern approach: Parse HTML and replace document content
        const parser = new DOMParser();
        const newDoc = parser.parseFromString(html, "text/html");

        // Replace the entire document
        document.documentElement.innerHTML = newDoc.documentElement.innerHTML;

        // Update document title if it exists in the new content
        if (newDoc.title) {
            document.title = newDoc.title;
        }

        executeNewScripts();
    } catch (error) {
        console.error("Failed to replace document:", error);
        window.location.reload();
    }
}

function executeNewScripts(): void {
    const scripts = document.querySelectorAll("script:not([data-executed])");

    scripts.forEach((element) => {
        const oldScript = element as HTMLScriptElement;
        oldScript.setAttribute("data-executed", "true");

        const newScript = document.createElement("script");
        Array.from(oldScript.attributes).forEach((attr) => {
            newScript.setAttribute(attr.name, attr.value);
        });

        if (oldScript.src) {
            newScript.src = oldScript.src;
        } else {
            newScript.textContent = oldScript.textContent;
        }

        if (oldScript.parentNode) {
            oldScript.parentNode.replaceChild(newScript, oldScript);
        }
    });
}
