//==========================================================================================
// Icon: material-symbols:error-outline
// Slug: Shows validation error for a field.
// Description: Displays the first validation error message for a specified field from the errors signal.

import type { AttributePlugin } from "../../../datastar/engine/types";
import { pathToObj } from "../../../datastar/utils/paths";

export const Error: AttributePlugin = {
    type: "attribute",
    name: "error",
    keyReq: "denied",
    valReq: "must",
    shouldEvaluate: false,
    onLoad: ({ el, value, effect, computed, getPath, mergePatch }) => {
        const fieldName = value.trim();

        // Ensure the 'errors' signal exists and is initialized as an empty object
        // This works like the bind plugin - creates the signal if missing
        mergePatch(pathToObj({}, { errors: {} }), { ifMissing: true });

        // Create a computed that reactively reads the error for this field
        const errorComputed = computed(() => {
            const errors = getPath("errors");
            if (errors && errors[fieldName]) {
                const fieldErrors = errors[fieldName];
                if (Array.isArray(fieldErrors) && fieldErrors.length > 0) {
                    return fieldErrors[0];
                } else if (typeof fieldErrors === "string") {
                    return fieldErrors;
                }
            }
            return null;
        });

        // Effect that updates the DOM when the error changes
        const cleanup = effect(() => {
            const errorMessage = errorComputed();

            if (errorMessage) {
                el.style.removeProperty("display");
                el.textContent = errorMessage;
            } else {
                el.style.setProperty("display", "none");
                el.textContent = "";
            }
        });

        return cleanup;
    },
};
//=======================================================================================
