// packages/dancycodes/hyper-front/src/hyper-ext/plugins/actions/fileUrl.ts

// Icon: material-symbols:attachment
// Slug: Converts file sources to valid HTML src/href URLs with smart fallbacks.
// Description: Handles base64 arrays from file binding and provides fallbacks. File paths are auto-converted by Laravel backend.

import type {
    ActionPlugin,
    RuntimeContext,
} from "../../../datastar/engine/types";

/**
 * FileUrl Action - Simplified and Reliable
 *
 * Now much simpler since Laravel handles path-to-URL conversion.
 * Frontend only needs to handle:
 * - Base64 arrays from Datastar file inputs
 * - URLs (already converted by Laravel)
 * - Fallbacks for missing/empty data
 *
 * Usage Examples:
 * @fileUrl($profilePicture)                           // Auto-detect with fallback
 * @fileUrl($document, {fallback: '/default.pdf'})     // Custom fallback
 * @fileUrl('/storage/file.jpg')                       // URL passthrough
 * @fileUrl($avatar, {defaultMime: 'image/png'})       // Custom MIME for base64
 */
export const FileUrl: ActionPlugin = {
    type: "action",
    name: "fileUrl",
    fn: (
        _ctx: RuntimeContext,
        fileSource: any,
        options?: {
            fallback?: string;
            defaultMime?: string;
            mimeSignal?: string; // Name of companion MIME signal
        }
    ): string => {
        const opts = options || {};
        const fallback = opts.fallback || "";
        const defaultMime = opts.defaultMime || "application/octet-stream";
        const mimeSignal = opts.mimeSignal;

        // Handle null, undefined, or empty values
        if (fileSource == null) {
            return fallback;
        }

        // Handle arrays (from Datastar file binding)
        if (Array.isArray(fileSource)) {
            // Empty array means no file selected
            if (fileSource.length === 0) {
                return fallback;
            }

            const base64Content = fileSource[0];
            if (!base64Content || typeof base64Content !== "string") {
                return fallback;
            }

            // Determine MIME type
            let mimeType = defaultMime;

            // Try to get MIME from companion signal if specified
            if (mimeSignal && typeof window !== "undefined") {
                const mimeArray = ((window as any).$ || {})[mimeSignal];
                if (Array.isArray(mimeArray) && mimeArray.length > 0) {
                    mimeType = mimeArray[0] || defaultMime;
                }
            }

            // Construct data URL
            return `data:${mimeType};base64,${base64Content}`;
        }

        // Handle strings (URLs, paths, data URLs)
        if (typeof fileSource === "string") {
            const trimmed = fileSource.trim();

            // Empty string
            if (!trimmed) {
                return fallback;
            }

            // Laravel should have converted file paths to proper URLs
            // We just return whatever string we got
            return trimmed;
        }

        // Handle unexpected types gracefully
        if (typeof fileSource === "object") {
            console.warn(
                "FileUrl action received unexpected object:",
                fileSource
            );
        }

        return fallback;
    },
};
